.story-container {
  margin-bottom: 60px;
  padding: 0 15px; }
  .story-container h1 {
    margin: 35px 0 0;
    font-size: 30px;
    line-height: 1.35; }
  .story-container h2 {
    font-size: 20px;
    line-height: 1.35; }
  .story-container p,
  .story-container li,
  .story-container div {
    line-height: 1.7;
    word-break: break-all; }
  .story-container img {
    display: block;
    margin: 0 auto;
    max-width: 100%; }
  .story-container figure.background {
    position: relative;
    margin: 0 -15px;
    height: 300px;
    overflow: hidden; }
    .story-container figure.background img {
      display: block;
      margin-top: -10%; }
    .story-container figure.background p {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 100%;
      padding: 2px 5px;
      margin: 0;
      font-size: 12px;
      color: #fff;
      background: rgba(50, 50, 50, 0.6); }
  .story-container figure {
    margin: 16px 0; }
  .story-container .meta img {
    display: inline-block;
    margin-right: 10px;
    width: 24px;
    height: 24px; }
  .story-container .meta * {
    vertical-align: middle; }
  .story-container .content {
    margin: 16px 0; }
  .story-container .story-comment-list-header {
    margin: 15px -15px;
    padding: 15px;
    background-color: #f5f5f5; }
  .story-container .story-content {
    margin-bottom: 50px; }

@media screen and (max-width: 767px) {
  figure.background {
    -webkit-box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12); } }

@media screen and (min-width: 768px) {
  .story-container figure.background {
    height: 500px; }
    .story-container figure.background img {
      width: 100%; } }



