.top-story {
  position: relative;
  height: 100%; }
  .top-story:first-child {
    grid-column: 1 / 3; }
  .top-story-image {
    height: 90px; }
    .top-story-image img {
      width: 100%;
      height: 100%; }
  .top-story-link, .top-story-link:link, .top-story-link:hover {
    width: 100%;
    margin: 0;
    color: #000;
    font-size: 15px;
    line-height: 1.45;
    font-weight: bold; }
    .top-story-link p, .top-story-link:link p, .top-story-link:hover p {
      margin: 0;
      margin-top: 10px; }

.top-stories {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  padding: 8px 8px 0 8px; }

@media screen and (min-width: 1023px) {
  .top-stories {
    padding: 0; }
  .top-story-image {
    min-height: 120px; } }



