.nav {
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 2; }
  .nav .nav-header {
    background: #fff;
    color: #333;
    -webkit-box-shadow: 0px 2px 4px -1px rgba(200, 200, 200, 0.2), 0px 4px 5px 0px rgba(200, 200, 200, 0.14), 0px 1px 10px 0px rgba(200, 200, 200, 0.12);
    box-shadow: 0px 2px 4px -1px rgba(200, 200, 200, 0.2), 0px 4px 5px 0px rgba(200, 200, 200, 0.14), 0px 1px 10px 0px rgba(200, 200, 200, 0.12); }

.nav-title {
  -ms-flex: 1 1;
  flex: 1 1;
  padding-left: 10px;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  overflow: hidden; }

.nav-github {
  width: 24px;
  height: 24px; }
  .nav-github img {
    width: 100%;
    height: 100%; }



